import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { FieldMessage } from './../../../models/config/field-message';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'alert-field-mensagem',
  styles: ['.alert .alert-icon-col {min-width: 25px;max-width: 25px;} body {padding: 10px;}'],
  template: `
        <div *ngIf="fields?.length > 0" class="alert alert-danger" role="alert">
            <h4 class="alert-heading">Erro de validação!</h4>
            <div class="row">
            <div class="col-1 alert-icon-col">
                <span class="fa fa-exclamation-triangle fa-fw"></span>
            </div>
            <div class="col">
              <p *ngFor="let error of fields">
                <strong>{{error.fieldName}}</strong><span *ngIf="error.fieldName"> - </span>{{error.message}}<br/>
              </p>
            </div>
            </div>
        </div>
   `
})
export class AlertFieldMensagemComponent implements OnInit {

  @Input() fields: FieldMessage[];

  constructor() { }

  ngOnInit() {
    EventEmitterService.getErrors().subscribe(res => this.fields = res);
  }

}



