import { ModalModule } from 'ng2-bootstrap/modal';
import { ImageService } from './../../../services/image.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';

import { AppRoutingModule } from './../../app.routing';

import { SharedComponentModule } from './../../shared/shared-component.module';
import { PopupModalModule } from './../../shared/popup/popup-modal.module';
import { BsDropdownModule } from 'ng2-bootstrap/dropdown';
import { AsideToggleDirective } from './../../shared/directive/aside.directive';
import { SIDEBAR_TOGGLE_DIRECTIVES } from './../../shared/directive/sidebar.directive';
import { NAV_DROPDOWN_DIRECTIVES } from './../../shared/directive/nav-dropdown.directive';
import { BreadcrumbsComponent } from './../../shared/components/breadcrumb.component';

import { FooterComponent } from './footer/footer.component';
import { MenuSideBarComponent } from './menu-side-bar/menu-side-bar.component';
import { HeardSideBarComponent } from './heard-side-bar/heard-side-bar.component';
import { UpSideBarComponent } from './up-side-bar/up-side-bar.component';
import { FullLayoutComponent } from './base/full-layout.component';
import { SimpleLayoutComponent } from './base/simple-layout.component';
import { AuthService } from 'services/auth.service';
import { UsuarioModule } from '../../pages/cadastros/usuario/usuario.module';

@NgModule({
    imports: [
        CommonModule,
        FormsModule,
        HttpModule,
        AppRoutingModule,
        PopupModalModule,
        SharedComponentModule,
        BsDropdownModule.forRoot(),
        ModalModule.forRoot(),
        UsuarioModule
    ],
    declarations: [
        AsideToggleDirective,
        UpSideBarComponent,
        NAV_DROPDOWN_DIRECTIVES,
        SIDEBAR_TOGGLE_DIRECTIVES,
        FullLayoutComponent,
        SimpleLayoutComponent,
        MenuSideBarComponent,
        FooterComponent,
        BreadcrumbsComponent,
        HeardSideBarComponent,
        FooterComponent
    ],
    providers:[
      AuthService,
      ImageService
    ]

}
)
export class LayoutModule { }



