import { UsuarioDTO } from './../../../../../models/domain/usuario.dto';
import { UsuarioService } from './../usuario.service';
import { TrataErrorService } from 'services/trata-error.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { FieldMessage } from './../../../../../models/config/field-message';
import { EstadoDTO } from './../../../../../models/domain/estado.dto';
import { ConsultaCEPService } from './../../../../../services/consulta-cep-service';
import { EnderecoDTO } from './../../../../../models/domain/endereco.dto';
import { API_CONFIG } from 'config/core/api.config';
import { CategoriaDTO } from 'models/domain/categoria.dto';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';



@Component({
  selector: 'usuario-popup-modal',
  templateUrl: './usuario-popup-modal.component.html',
  styleUrls: ['./usuario-popup-modal.component.scss']
})
export class UsuarioPopupModalComponent extends AbstractPopupComponent implements OnInit {

  usuario: UsuarioDTO = {};
  isAlteracao: boolean;

  constructor(
    public fb: FormBuilder,
    public usuarioService: UsuarioService) {
    super('usuarioPopup');
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastro de usuário")
  }

  showAlteracao(item: UsuarioDTO) {

    this.alterarForm(item)
    this.showPopup("Alterar usuário");
  }

  btnConfirmar(usuario: UsuarioDTO) {
    if (!this.isAlteracao && usuario.dsSenha != usuario.senhaConfirmacao) {
      TrataErrorService.emitirMensagemError("Senhas não conferem.")
    } else {
      this.acao(usuario, this.usuarioService);
    }
  }

  consultarCpf(nrCpf: string) {
    if (!this.isAlteracao && nrCpf != '') {
      this.usuarioService.consultarPorCpf(nrCpf)
        .subscribe(
          res => {
            this.alterarForm(res);
            TrataErrorService.emitirMensagemError("Já exite um usuario cadastrado com o CPF informado.")
          },
          error => TrataErrorService.limparMensagemError())
    }
  }
  consultarLogin(dsLogin: string) {
    if (dsLogin != '' && this.usuario.dsLogin != dsLogin) {
      this.usuarioService.consultarPorLogin(dsLogin)
        .subscribe(
          res => {
            if (res.id != this.usuario.id) {
              TrataErrorService.emitirMensagemError("o login informado já está em uso")
            }
          }, error => TrataErrorService.limparMensagemError())
    }
  }
  consultarEdEmail(edEmail: string) {
    if (edEmail != '') {
      this.usuarioService.consultarPorEdEmail(edEmail)
        .subscribe(
          res => {
            if (res.id != this.usuario.id) {
              TrataErrorService.emitirMensagemError("o e-mail informado já está em uso")
            }
          }, error => TrataErrorService.limparMensagemError())
    }
  }

  private alterarForm(usuario: UsuarioDTO) {
    this.isAlteracao = true;
    this.usuario = usuario;
    this.formulario.patchValue({
      id: usuario.id,
      nmPessoa: usuario.nmPessoa,
      nrDocumento: usuario.nrDocumento,
      dsLogin: usuario.dsLogin,
      edEmail: usuario.edEmail,
      dsSenha: usuario.dsSenha,
      senhaConfirmacao: usuario.senhaConfirmacao,
      status: usuario.status,
      perfil: usuario.perfil,
      emAlteracao: true
    })

  }

  private criarForm() {
    this.isAlteracao = false;
    this.formulario = this.fb.group({
      id: [''],
      nmPessoa: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(120)]],
      nrDocumento: ['', [Validators.required]],
      edEmail: [''],
      dsLogin: [''],
      dsSenha: [''],
      senhaConfirmacao: [''],
      status: [''],
      perfil: ['A'],
      emAlteracao: false
    })
  }

}


