import { API_CONFIG } from 'config/core/api.config';
import { TrataErrorService } from 'services/trata-error.service';
import { LogService } from './../../../../services/log.service';
import { AuthService } from './../../../../services/auth.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CredenciaisDTO } from 'models/config/credenciais.dto';



@Component({
  templateUrl: 'login.component.html'
})
export class LoginComponent implements OnInit {

  creds: CredenciaisDTO = {};
  versao: string;

  constructor(public auth: AuthService, private router: Router, private log: LogService) {
  }

  ngOnInit() {
    this.versao = API_CONFIG.versao;
    if (this.auth.hasToken()) {
      this.auth.refreshToken()
        .subscribe(
          response => this.auth.successfulLogin(response['headers'].get('Authorization')),
          error => TrataErrorService.tratarError(error));
    }
  }

  login() {
    this.auth.authenticate(this.creds)
      .subscribe(
        response => {
          this.auth.successfulLogin(response.headers.get('Authorization'));
          this.router.navigate(['dashboard'])
        },
        error => TrataErrorService.tratarError(error));
  }
}



