import { Router } from '@angular/router';
import { AbstractService } from 'config/core/abstract-service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from "@angular/core";
import { CredenciaisDTO } from "../models/config/credenciais.dto";
import { API_CONFIG } from "../config/core/api.config";
import { LocalUser } from "../models/config/local-user";
import { StorageService } from "./storage.service";
import { JwtHelper } from 'angular2-jwt';

@Injectable()
export class AuthService extends AbstractService {

    jwtHelper: JwtHelper = new JwtHelper();

    constructor(
        public http: HttpClient,
        public router: Router,
        public storage: StorageService) {
        super();
    }

    authenticate(creds: CredenciaisDTO) {
        return this.http.post(`${API_CONFIG.baseUrl}/login`, creds, { observe: 'response', responseType: 'text' });
    }

    refreshToken() {
        return this.http.post(`${API_CONFIG.baseUrl}/auth/refresh_token`, {}, this.getHearderTokenNoResponse());
    }

    successfulLogin(authorizationValue: string) {
        let tok = authorizationValue.substring(7);
        let user: LocalUser = {
            token: tok,
            email: this.jwtHelper.decodeToken(tok).sub
        };
        this.storage.setLocalUser(user);
        //this.cartService.createOrClearCart();
    }

    logouts() {
        this.storage.setLocalUser(null);
    }

    isAuthenticated() {
        let user = this.storage.getLocalUser();
        if (user != null) {
            return !this.jwtHelper.isTokenExpired(user.token);
        }
        return false;
    }
    public hasToken() {
        let user = this.storage.getLocalUser();
        return user != null && user.token != null;
    }

    logoutSistema() {
        this.storage.setLocalUser(null);
        this.router.navigate(['auth/login']);
    }

}



