
import { Component, Input, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';
import { ModalDirective } from 'ng2-bootstrap/modal/modal.component';


@Component({
  selector: 'confirm-popup-modal',
  styleUrls: ['./confirm-popup-modal.component.scss'],
  templateUrl: './confirm-popup-modal.component.html'

})
export class ConfirmPopupModalComponent implements OnInit {

  @ViewChild('modalConfirm') confirma;
  @Input() titulo: string = "Confirmar";
  @Input() popupStyle: string = '';
  @Output() onClickConfirm = new EventEmitter<boolean>();

  constructor() {
  }

  ngOnInit() {
  }

  public show() {
    this.confirma.show();
  }

  btnConfirmar() {
    this.confirma.hide();
    this.onClickConfirm.emit(true);
  }

  public getPopupStyle() {
    let cssClasses={};
    if (this.popupStyle == '') {
      cssClasses['modal-primary']= true
    } else{
      cssClasses['modal-'+this.popupStyle]=true;
    }
    return cssClasses;
  }

}



