import { Pipe, PipeTransform } from "@angular/core";

@Pipe({ name: 'dataMask' })
export class DataPipe implements PipeTransform {

    transform(value: string) {
        value = value.replace(/\D/g, '');
        if (value && value.length === 8) {
            value = value.replace(/(\d{2})(\d{2})(\d{4})/, "$1/$2/$3");
            return value;
        }
    }
}


