import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'foto',
  template: '<img [src]="src" [class]="classCss" [alt]="alt" [style]="styleCss">'
})
export class FotoComponent implements OnInit {

  @Input() src: string;
  @Input() classCss: string;
  @Input() alt?: string;
  @Input() styleCss?:string;

  constructor() { }

  ngOnInit() {
  }

}



