
import { Component, Input, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';
import { ModalDirective } from 'ng2-bootstrap/modal/modal.component';

import { ImageCropperComponent, CropperSettings } from 'ng2-img-cropper';

@Component({
  selector: 'upload-image-modal',
  styleUrls: ['./upload-image-popup-modal.component.scss'],
  templateUrl: './upload-image-popup-modal.component.html'

})
export class UploadImagePopupModalComponent implements OnInit {

  @ViewChild('modalUploadImagem') confirma;
  @ViewChild('cropper', undefined) cropper: ImageCropperComponent;
  @Output() onClickConfirm = new EventEmitter();

  botaoTitulo: string ="Selecione uma imagem"

  data : any;
  cropperSettings: CropperSettings;



  constructor() {
    this.cropperSettings = new CropperSettings();
    this.cropperSettings.width = 200;
    this.cropperSettings.height = 200;

    this.cropperSettings.croppedWidth = 200;
    this.cropperSettings.croppedHeight = 200;

    this.cropperSettings.canvasWidth = 270;
    this.cropperSettings.canvasHeight = 265;
    this.cropperSettings.noFileInput = true;
    this.data =  new Image();
  }

  ngOnInit() {
  }

  public show() {
    this.confirma.show();
  }

  btnConfirmar() {
    this.confirma.hide();
    this.onClickConfirm.emit({ foto: this.data.image });
  }


  fileChangeListener($event) {
    const image: any = new Image();
    const file: File = $event.target.files[0];
    const myReader: FileReader = new FileReader();
    const that = this;
    myReader.onloadend = function (loadEvent: any) {
      image.src = loadEvent.target.result;
      that.cropper.setImage(image);
    };
    myReader.readAsDataURL(file);
  }
}



