import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'status',
  template: '<span  class="badge badge-{{class}}">{{texto}}</span>'
})
export class StatusComponent {
  @Input() status: string;
  class: string;
  texto: string;

  constructor() { }

  ngOnInit() {
    switch (this.status) {
      case 'I':
        this.texto = 'Inativo'
        this.class = 'danger';
        break;
      default:
        this.texto = 'Ativo'
        this.class = 'success'
        break;
    }
  }

}



