import { Component, Output, ViewChild, OnInit, EventEmitter, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { EventEmitterService } from './../../event/event-emitter.services';
import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { TrataErrorService } from 'services/trata-error.service';

import { ComboDTO } from './../../../../models/domain/combo.dto';
import { CategoriaService } from './../../../pages/config/categoria/categoria.service';
import { ClienteService } from './../../../pages/cadastros/cliente/cliente.service';
import { ClienteDTO } from './../../../../models/domain/cliente.dto';
import { ClienteConsDTO } from './../../../../models/domain/cliente-cons.dto';

@Component({
  selector: 'pesquisar-cliente-popup-modal',
  templateUrl: './pesquisar-cliente-popup-modal.component.html'
})
export class PesquisarClienteModalComponent extends AbstractPopupComponent implements OnInit {

  items: ClienteConsDTO[] = [];
  cliente = {} as ClienteDTO;
  desabilitarBtnSelecione: boolean = true;
  @ViewChild('modalConfirm') modalConfirm;
  

  constructor(
    public clienteService: ClienteService,
    public fb: FormBuilder) {
    super('pesquisarClientePopupModal');
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Pesquisa de clientes");
  }


  pesquisarCliente(form: any) {
    this.clienteService.findByNrCpfOrNmPessoa(form.nrCpf, form.nmPessoaFisica).
      subscribe(
        res => this.items = res,
        error => TrataErrorService.tratarError(error)
      );
  }

  selecionarCliente(cliente: ClienteDTO) {
    this.cliente = cliente;
    this.cliente.id = cliente.id;
    this.cliente.nmPessoa = cliente.nmPessoa;
    this.modalConfirm.btnBaseConfirmar();
  }
  
  
  btnConfirmar() {
    this.onClickConfirm.emit(this.cliente);
    EventEmitterService.closePopup(this.nomeJanePopup);
  }

  private criarForm() {
    this.items = [];
    this.formulario = this.fb.group({
      nrCpf: [''],
      nmPessoaFisica: ['']
    })
  }


}



