import { Component } from '@angular/core';

import { ToasterModule, ToasterService, ToasterConfig } from 'angular2-toaster/angular2-toaster';
import { ToasterMensagem } from 'models/config/toater-mensagem.model';

import { EventEmitterService } from 'app/shared/event/event-emitter.services';



@Component({
  selector: 'body',
  templateUrl: './app.component.html'
})
export class AppComponent {

  public toasterconfig: ToasterConfig = new ToasterConfig({
    tapToDismiss: true,
    newestOnTop: true,
    positionClass: 'toast-top-full-width',
    timeout: 3500
  });


  constructor(private toasterService: ToasterService) {
    this.toasterService = toasterService;
  }

  ngOnInit() {
    EventEmitterService.getToater().subscribe(res => this.showToasterMessage(res));
  }

  public showToasterMessage(tm: ToasterMensagem) {
    this.toasterService.clear();
    this.toasterService.pop(tm.getTipoMensagem(), tm.getTitulo(), tm.getMensagem());
  }
}



