import { EventEmitterService } from './../../event/event-emitter.services';
import { ConsultaCEPService } from 'services/consulta-cep-service';
import { TrataErrorService } from 'services/trata-error.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter, Input } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';

import { EnderecoDTO } from './../../../../models/domain/endereco.dto';

@Component({
  selector: 'cep-popup-modal',
  templateUrl: './cep-popup-modal.component.html'
})
export class CepModalComponent extends AbstractPopupComponent implements OnInit {

  item: EnderecoDTO;

  constructor(
    public consultaCEPService: ConsultaCEPService,
    public fb: FormBuilder) {
    super('cepPopupModal');
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastra endereco");
  }

  showAlteracao(item: EnderecoDTO) {
    console.log('endereco',item)
    this.item = item;
    this.alterarForm(item)
    this.showPopup("Alterar endereço");
  }

  public consultarCEP(nrCep: string) {
    let cep = nrCep.replace(/\D/g, '');
    this.consultaCEPService.consultarCEP(nrCep)
      .subscribe(
        res => {
          this.alterarFormCep(res);
          TrataErrorService.limparMensagemError();
        },
        error => TrataErrorService.emitirMensagemError("O CEP informado não foi localizado")
      )
  }

  btnConfirmar(value: EnderecoDTO) {
    EventEmitterService.closePopup(this.nomeJanePopup);
    if (value.emAlteracao) {
      this.item = value;
      /*
      this.item.id = value.id;
      this.item.idPessoa = value.idPessoa;
      this.item.idLogradouro = value.idLogradouro;
      this.item.dsEndereco = value.dsEndereco;
      this.item.nrCep = value.nrCep;
      this.item.dsReferencia = value.dsReferencia;
      this.item.nmLogradouro = value.nmLogradouro;
      this.item.nrComplemento = value.nrComplemento;
      this.item.txComplemento = value.txComplemento;
      this.item.nmBairro = value.nmBairro;
      this.item.nmMunicipio = value.nmMunicipio;
      this.item.uf = value.uf;
      */
      this.onClickConfirm.emit(this.item);
    } else {
      this.onClickConfirm.emit(value);
    }
  }

  private criarForm() {
    this.formulario = this.fb.group({
      id: [],
      idPessoa:[''],
      idLogradouro:[''],
      dsEndereco: [''],
      dsReferencia: [''],
      nmLogradouro: [''],
      nrComplemento: ['', Validators.required],
      txComplemento: [''],
      nmBairro: [''],
      nrCep: ['', Validators.required],
      nmMunicipio: [''],
      uf: [''],
      emAlteracao: false
    })
  }

  private alterarFormCep(endereco: EnderecoDTO) {
    this.formulario.patchValue({
      idLogradouro:endereco.idLogradouro,
      nmBairro: endereco.nmBairro,
      nmLogradouro: endereco.nmLogradouro,
      nmMunicipio: endereco.nmMunicipio,
      uf: endereco.uf,
    })
  }
  private alterarForm(endereco: EnderecoDTO) {
    this.alterarFormCep(endereco);
    this.formulario.patchValue({
      id: endereco.id,
      idPessoa: endereco.idPessoa,
      idLogradouro:endereco.idLogradouro,
      dsEndereco: endereco.dsEndereco,
      dsReferencia: endereco.dsEndereco,
      nmLogradouro: endereco.nmLogradouro,
      nrComplemento: endereco.nrComplemento,
      txComplemento: endereco.txComplemento,
      nmBairro: endereco.nmBairro,
      nrCep: endereco.nrCep,
      nmMunicipio: endereco.nmMunicipio,
      uf: endereco.uf,
      emAlteracao: true
    })
  }

}



