import { API_CONFIG } from 'config/core/api.config';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs/Rx";
import { Injectable } from '@angular/core';

import { AbstractService } from 'config/core/abstract-service';
import { AdicionalCategoriaDTO } from './../../../../models/domain/adicional-categoria.dto';

@Injectable()
export class AdicionalProdutoService extends AbstractService {

    url: string = `${API_CONFIG.baseUrl}/adicionais`;

    constructor(public http: HttpClient) {
        super();
    }

    findAll(): Observable<AdicionalCategoriaDTO[]> {
        return this.http.get<AdicionalCategoriaDTO[]>(`${this.url}/all`, this.getHearderToken());
    }

    findByCategoria(idCategoria: string): Observable<AdicionalCategoriaDTO[]> {
        console.log(idCategoria);
        return this.http.get<AdicionalCategoriaDTO[]>(`${this.url}/categoria/${idCategoria}`, this.getHearderToken());
    }

    findAllAtivo(): Observable<AdicionalCategoriaDTO[]> {
        return this.http.get<AdicionalCategoriaDTO[]>(`${this.url}/`);
    }

    findById(id: string): Observable<{}> {
        return this.http.get(`${this.url}/${id}`, this.getHearderToken());
    }

    salvar(info: AdicionalCategoriaDTO) {
        return this.http.post(`${this.url}/`, JSON.stringify(info), this.getHearderTokenNoResponse());
    }

    changeStatus(info: AdicionalCategoriaDTO) {
        return this.http.put(`${this.url}/${info.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
    }

    atualizar(info: AdicionalCategoriaDTO) {
        return this.http.put(`${this.url}/${info.id}`, JSON.stringify(info), this.getHearderTokenNoResponse());
    }

    excluir(info: AdicionalCategoriaDTO): Observable<{}> {
        let url = `${this.url}/${info.id}`;
        return this.http.delete(url, this.getHearderToken());
    }
}



