import { AdicionalProdutoService } from './../../pages/config/adicional/adicional-produto.service';
import { SelectModule } from 'ng2-select';
import { PesquisarProdutoModalComponent } from './produto/pesquisar-produto-popup-modal.component';
import { CategoriaService } from './../../pages/config/categoria/categoria.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TextMaskModule } from 'angular2-text-mask';
import { ConsultaCEPService } from './../../../services/consulta-cep-service';
import { CepModalComponent } from './cep/cep-popup-modal.component';
import { BasePopupModalComponent } from './modal/base-popup-modal.component';
import { ConfirmPopupModalComponent } from './confirm/confirm-popup-modal.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ModalModule } from 'ng2-bootstrap/modal';
import { SharedComponentModule } from '../shared-component.module';


import { PesquisarClienteModalComponent } from './cliente/pesquisar-cliente-popup-modal.component';
import { ClienteService } from '../../pages/cadastros/cliente/cliente.service';
import { ProdutoService } from '../../pages/cadastros/produto/produto.service';
// DataTable


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    SelectModule,
    TextMaskModule
  ],
  declarations: [
    ConfirmPopupModalComponent,
    BasePopupModalComponent,
    CepModalComponent,
    PesquisarClienteModalComponent,
    PesquisarProdutoModalComponent
  ],
  exports: [
    ConfirmPopupModalComponent,
    BasePopupModalComponent,
    CepModalComponent,
    PesquisarClienteModalComponent,
    PesquisarProdutoModalComponent
  ],
  providers:[
      ConsultaCEPService,
      ClienteService,
      ProdutoService, 
      CategoriaService,
      AdicionalProdutoService
  ]
  
})
export class PopupModalModule { }



