import { ImageService } from './../../../../services/image.service';
import { API_CONFIG } from './../../../../config/core/api.config';
import { UsuarioDTO } from './../../../../models/domain/usuario.dto';

import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { UsuarioService } from '../../../pages/cadastros/usuario/usuario.service';
import { TrataErrorService } from './../../../../services/trata-error.service';

@Component({
  selector: 'heard-side-bar',
  templateUrl: './heard-side-bar.component.html'
})
export class HeardSideBarComponent implements OnInit {

  idUsuario: string = "";
  nmUsuario: string = "";
  urlFotoPerfil: string = '/assets/img/noimage/prod.jpg';

  constructor(private usuarioService: UsuarioService, public image: ImageService) { }

  ngOnInit() {
    this.usuarioService.infoUsuarioLogado()
      .subscribe(
        res => {
          this.idUsuario = res.id;
          this.nmUsuario = res.nmPessoa
          this.image.getImageFromBucket(this.idUsuario, 'CLI').subscribe(res => this.urlFotoPerfil = `${API_CONFIG.bucketBaseUrl}/clientes/cli${this.idUsuario}.jpg`)
        }, error => TrataErrorService.tratarError(error)
      );
  }

}



