import { ImageCropperComponent } from 'ng2-img-cropper';
import { UploadImagePopupModalComponent } from './components/upload-imagem/modal/upload-image-popup-modal.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { BsDropdownModule } from 'ng2-bootstrap/dropdown';
import { TextMaskModule } from 'angular2-text-mask';
import { SelectModule } from 'ng2-select';

import { TelefonePipe } from './pipe/telefone.pipe';
import { CpfCnpjPipe } from './pipe/cpf-cnpj.pipe';
import { CepPipe } from './pipe/cep.pipe';
import { DataPipe } from './pipe/data.pipe';
import { CnpjPipe } from './pipe/cnpj.pipe';
import { CpfPipe } from './pipe/cpf.pipe';

import { DisableControlDirective } from './directive/disabled-control.directive';
import { LowerCaseTextDirective } from './directive/lower-case.directive';
import { UpperCaseTextDirective } from './directive/upper-case.directive';
import { AutoFocoDirective } from './directive/auto-foco.directive';
import { NumberOnlyDirective } from './directive/number-only.directive';

import { DestaqueComponent } from './components/destaque.component';
import { FormButtonsComponent } from './components/form-buttons.component';
import { StatusPedidoComponent } from './components/status-pedido.component';
import { OrigemPedidoComponent } from './components/origem-pedido.component';
import { AlertFieldMensagemComponent } from './components/alert-field-mensagem.component';
import { UploadImagemComponent } from './components/upload-imagem/upload-imagem.component';
import { FormDebugComponent } from './components/form-debug.component';
import { AlertMensagemComponent } from './components/alert-mensagem.component';
import { FotoComponent } from './components/foto.component';
import { SimNaoComponent } from './components/sim-nao.component';
import { StatusComponent } from './components/status.component';
import { FotoURIComponent } from './components/foto-uri.component';

import { ImageService } from 'services/image.service';
import { ModalModule } from 'ng2-bootstrap/modal';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    TextMaskModule,
    SelectModule,
    BsDropdownModule,
    ModalModule.forRoot(),
  ],
  declarations: [
    FotoComponent,
    FotoURIComponent,
    AlertMensagemComponent,
    AlertFieldMensagemComponent,
    FormDebugComponent,
    UploadImagemComponent,
    StatusComponent,
    SimNaoComponent,
    AutoFocoDirective,
    NumberOnlyDirective,
    OrigemPedidoComponent,
    CpfCnpjPipe,
    CpfPipe,
    CepPipe,
    CnpjPipe, 
    DataPipe,
    TelefonePipe,
    UpperCaseTextDirective,
    LowerCaseTextDirective,
    DisableControlDirective,
    StatusPedidoComponent,
    FormButtonsComponent,
    DestaqueComponent,
    UploadImagePopupModalComponent,
    ImageCropperComponent
    
  ],
  exports: [
    FotoComponent,
    FotoURIComponent,
    AlertMensagemComponent,
    AlertFieldMensagemComponent,
    FormDebugComponent,
    UploadImagemComponent,
    StatusComponent,
    SimNaoComponent,
    AutoFocoDirective,
    NumberOnlyDirective,
    CpfCnpjPipe,
    CepPipe,
    CpfPipe,
    CnpjPipe,
    DataPipe,
    TelefonePipe,
    UpperCaseTextDirective,
    LowerCaseTextDirective,
    DisableControlDirective,
    OrigemPedidoComponent,
    StatusPedidoComponent,
    FormButtonsComponent,
    DestaqueComponent,
    UploadImagePopupModalComponent,
    ImageCropperComponent
  ],
  providers:[
    ImageService
  ]
})
export class SharedComponentModule { }



