import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'simnao',
  template: '<span  class="badge badge-{{class}}">{{texto}}</span>'
})
export class SimNaoComponent {
  @Input() valor: string;
  class: string;
  texto: string;

  constructor() { }

  ngOnInit() {
    switch (this.valor) {
      case 'N':
        this.texto = 'Não'
        this.class = 'danger'
      break;
      default:
        this.texto = 'Sim'
        this.class = 'success'
      break;
    }
  }

}



