import { Injectable } from  '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';

import { API_CONFIG } from './../../../../config/core/api.config';
import { CategoriaDTO } from 'models/domain/categoria.dto';
import { AbstractService } from 'config/core/abstract-service';

import { Observable } from 'rxjs/Rx';
//import { catchError, retry } from 'rxjs/operators';


@Injectable()
export class CategoriaService extends AbstractService {
  
  url = `${API_CONFIG.baseUrl}/categorias`;

  constructor(public http: HttpClient){
    super();
  }

  findById(id: string): Observable<{}> {
    return this.http.get(`${this.url}/${id}`,this.getHearderToken() );
  }

  findCategoriasAtivasExcetoInterna(moduloConsulta: string): Observable<CategoriaDTO[]> {   
    if (moduloConsulta =='CRD'){
      return this.http.get<CategoriaDTO[]>(`${API_CONFIG.baseUrl}/cardapio/categoria`);
    }else{
      return this.http.get<CategoriaDTO[]>(`${this.url}/`,this.getHearderToken() );
    }
  }

  findAll(): Observable<CategoriaDTO[]> {
    return this.http.get<CategoriaDTO[]>(`${this.url}/all`, this.getHearderToken());
  }

  salvar(categoria: CategoriaDTO) {
    return this.http.post(`${this.url}`, JSON.stringify(categoria), this.getHearderTokenNoResponse());
  }

  changeStatus(categoria: CategoriaDTO) {
    return this.http.put(`${this.url}/${categoria.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
  }

  atualizar(categoria: CategoriaDTO) {
    return this.http.put(`${this.url}/${categoria.id}`, JSON.stringify(categoria), this.getHearderTokenNoResponse());
  }

  excluir(categoria: CategoriaDTO): Observable<{}> {
    return this.http.delete(`${this.url}/${categoria.id}`, this.getHearderToken());
  }

  insert(obj: any) {
    return this.http.post(`${this.url}/`, obj, { observe: 'response', responseType: 'text' });
  }
}



