import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { FormGroup } from '@angular/forms';
import { Component, Input, Output, ViewChild, OnInit, EventEmitter, OnDestroy } from '@angular/core';

@Component({
  selector: 'base-popup-modal',
  styleUrls: ['./base-popup-modal.component.scss'],
  templateUrl: './base-popup-modal.component.html'

})
export class BasePopupModalComponent implements OnInit, OnDestroy {

  @ViewChild('modalConfirm') confirma;

  @Input() titulo;

  @Input() nomePopup;

  @Input() exibirBtnSelecione : boolean = false;
 
  @Input() modalSize;

  @Input() desabilitarBtnConfirmar: boolean = false;
  
  @Input() ocultarBotoes: boolean = false;

  @Output() onClickBaseConfirm = new EventEmitter<boolean>();

  constructor() {
  }

  ngOnInit() {
    this.modalSize =this.modalSize== undefined? 'modal-lg':`modal-${this.modalSize}`;
    EventEmitterService.showPopup(this.nomePopup).subscribe(x => this.confirma.show());
    EventEmitterService.getPopupClose(this.nomePopup).subscribe(x => this.confirma.hide());
  }

  public btnBaseConfirmar() {
    this.onClickBaseConfirm.emit(true);
  }

  ngOnDestroy() {
    EventEmitterService.unsubscribeEvent();
  }

}



