import { TrataErrorService } from './../../../services/trata-error.service';
import { ImageService } from 'services/image.service';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'foto-uri',
  template: '<img [src]="src" [class]="classCss" [alt]="alt" [style]="styleCss">'
})
export class FotoURIComponent implements OnInit {

  src: string;
  @Input() id: string;
  @Input() modulo: string;
  @Input() classCss: string;
  @Input() alt?: string;
  @Input() styleCss?: string;


  constructor(public imagem: ImageService) { }

  ngOnInit() {
    this.src = '/assets/img/noimage/prod.jpg'
    
    this.getImageIfExists(this.id);
  }

  getImageIfExists(id: string) {
    if (id != "" && this.modulo != null && this.modulo != undefined) {
      this.imagem.getImageFromBucket(id, this.modulo)
        .subscribe(
          res => this.src = this.imagem.getImage(id, this.modulo),
          //error => this.src = this.imagem.getImage(id, this.modulo)
        )
    }
  }
}



