import { PedidoItemDTO } from './../../models/domain/pedido-item.dto';
import { FieldMessage } from './../../models/config/field-message';
import { ElementRef } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';

import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { SelectItem } from 'ng2-select';
import { ComboDTO } from 'models/domain/combo.dto';


export abstract class AbstractBaseComponent {

    constructor() {

    }

    public isErro: boolean = false;
    public errorMensagem: string ="";

    public maskCPF = [/\d/, /\d/, /\d/, '.', /\d/, /\d/, /\d/, '.', /\d/, /\d/, /\d/, '-', /\d/, /\d/];
    public maskCNPJ = [/\d/, /\d/, '.', /\d/, /\d/, /\d/, '.', /\d/, /\d/, /\d/, '/', /\d/, /\d/, /\d/, '-', /\d/, /\d/];
    public maskCEP = [/[0-9]/, /\d/, '.', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/];
    public maskFoneResidencial = ['(', /[0-9]/, /\d/, ')', ' ', /\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];
    public maskFoneCelular = ['(', /[0-9]/, /\d/, ')', ' ', /\d/, /\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];
    public maskData = [/\d/, /\d/, '/', /\d/, /\d/, '/', /\d/, /\d/, /\d/, /\d/];

    public formulario: FormGroup;

    protected urlFotoAvatar: string = 'assets/img/noimage/prod.jpg';
    //protected urlClienteAvatar: string = 'assets/img/noimage/noimagem.jpg';

    protected urlFoto: string = '';
    protected messageValidatorErros: FieldMessage[];

    public label: any = {
        selecione: ':: selecione ::'
    };

    public btn: any = {
        btnSalvar: 'Salvar',
        btnEditar: 'Editar',
        btnLimpar: 'Limpar',
        btnVoltar: 'Voltar',
        btnExcluir: 'Excluir',
        btnAtivar: 'Ativar',
        btnAlterarPerfil: 'Alterar perfil',
        btnAlterarSenha: 'Alterar senha',
        btnInativar: 'Inativar',
        btnNovo: 'Novo'
    };

    public msg: any = {
        msgRegistroIncluido: 'Registro incluído com sucesso!!!',
        msgRegistroAlterado: 'Registro alterado com sucesso!!!',
        msgRegistroExcluido: 'Registro excluído com sucesso!!!',
        msgSenhaAlterada: 'Senha alterada com sucesso!!!'
    };

    public casoDeUso: any = {
        frmCadastroUsuarioTitulo: 'Cadastro de usuário',
        frmCadastroUsuarioNovo: 'Formulário de cadastro de usuário',
        frmCadastroUsuarioLista: 'Listar usuários',
    };

    isInvalido(campo) {
        return !campo.valid && !campo.pristine;
    }

    isValido(campo) {
        return campo.valid && !campo.pristine;
    }

    getClassFormControl(campo: any): {} {
        return {
            'form-control': true,
            'form-control-danger': this.isInvalido(campo),
            'form-control-success': this.isValido(campo)
        };
    }

    getClassFormGroup(campo: any): {} {
        return {
            'form-group': true,
            'has-danger': this.isInvalido(campo),
            'has-success': this.isValido(campo)
        }
    }

    mensagemSucesso(titulo: string, mensagem: string) {
        EventEmitterService
            .emitirMensagemToasterSucesso(titulo, mensagem);
    }

    aplicarCssGroupRow(campo: FormGroup) {
        return {
            'form-group row ': true,
            'has-danger': this.isInvalido(campo),
            'has-success': this.isValido(campo)
        }
    }

    aplicarCssGroup(campo: FormGroup) {
        return {
            'form-group': true,
            'has-danger': this.isInvalido(campo),
            'has-success': this.isValido(campo)
        }
    }
    aplicarCssForm(campo: FormGroup) {
        return {
            'form-control': true,
            'form-control-danger': this.isInvalido(campo),
            'form-control-success': this.isValido(campo)
        }
    }

    public isCampoVazio(campo: any) {
        return campo.value == null || campo.value == '';
    }

    protected preencharCombo(response: any, compoId: string, campoValor: string) {
        let combo: ComboDTO[] = []
        if (response !=undefined && response.length >0){
            for (let cat of response) {
                combo.push(new ComboDTO(cat[compoId], cat[campoValor]));
            }
        }
        return combo;
    }

    protected fieldFocus(campo: ElementRef) {
        setTimeout(() => {
            campo.nativeElement.focus();
        }, 500);
    }

    protected calcularHashItem(item: PedidoItemDTO): string {
        let data = [];
        let x = item.produto.id + ';'
        for (let p of item.adicionais) {
            data.push(p.id);
        }
        data.sort(function (a, b) { return a - b; });
        for (let v of data) {
            x += v + ";"
        }

        return x;
    }

}



