import { SenhaUsuarioComponent } from './senha/senha-usuario.component';

import { NgModule, LOCALE_ID } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { SelectModule } from 'ng2-select';
import { LaddaModule } from 'angular2-ladda';
import { ModalModule } from 'ng2-bootstrap/modal';

import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { SharedComponentModule } from './../../../shared/shared-component.module';

import { TextMaskModule } from 'angular2-text-mask';
import { TabsModule } from 'ng2-bootstrap/tabs';


import { UsuarioPopupModalComponent } from './popup/usuario-popup-modal.component';
import { UsuarioService } from './usuario.service';
import { ListarUsuarioComponent } from './listar/listar-usuario.component';
import { UsuarioRoutingModule } from './usuario-routing.module';
import { SenhaPopupModalComponent } from './popup/senha-popup-modal.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    HttpClientModule,
    PopupModalModule,
    TabsModule,
    UsuarioRoutingModule,
    LaddaModule,
    TextMaskModule
  ],
  declarations: [
    ListarUsuarioComponent,
    UsuarioPopupModalComponent,
    SenhaPopupModalComponent,
    SenhaUsuarioComponent
  ],
  providers: [
    UsuarioService
  ]
})
export class UsuarioModule { }



