import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'status-pedido',
  template: '<span  class="badge badge-{{class}}">{{texto}}</span>'
})
export class StatusPedidoComponent {
  @Input() status: string;
  class: string;
  texto: string;

  constructor() { }

  ngOnInit() {
    switch (this.status) {
      case 'E':
        this.texto = 'Enviado'
        this.class = 'info';
        break;
      case 'P':
        this.texto = 'Em preparação'
        this.class = 'info';
        break;
      case 'C':
        this.texto = 'Cancelado'
        this.class = 'danger';
        break;
      case 'G':
        this.texto = 'Na rota de Entrega'
        this.class = 'primary';
        break;
      case 'T':
        this.texto = 'Entrega'
        this.class = 'success';
        break;
      }
  }
}



