import { ProdutoConsDTO } from './../../../../models/domain/produto-cons.dto';
import { PedidoItemDTO } from './../../../../models/domain/pedido-item.dto';
import { ProdutoDTO } from './../../../../models/domain/produto.dto';
import { AdicionalProdutoService } from './../../../pages/config/adicional/adicional-produto.service';
import { ComboDTO } from './../../../../models/domain/combo.dto';
import { CategoriaService } from './../../../pages/config/categoria/categoria.service';
import { Component, Output, ViewChild, OnInit, EventEmitter, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { EventEmitterService } from './../../event/event-emitter.services';
import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { TrataErrorService } from 'services/trata-error.service';

import { ProdutoService } from './../../../pages/cadastros/produto/produto.service';


@Component({
  selector: 'pesquisar-produto-popup-modal',
  templateUrl: './pesquisar-produto-popup-modal.component.html'
})
export class PesquisarProdutoModalComponent extends AbstractPopupComponent implements OnInit {

  @ViewChild('cmpOrdemItem') cmpOrdemItem;
  @Input() ocultarAcompanhamento: boolean = false;
  @Input() exibirCampoOrdem: boolean = false;
  @Input() moduloConsulta: string = 'PRD';

  ctrExibicaoCampoOrdem: boolean = false;

  //produto = {} as ProdutoDTO;
  desabilitarBtnSelecione: boolean = true;
  qtAcompanhamento: number = 0;
  qtItens: number = 0;
  produto: ProdutoDTO;

  //itens de consulta
  produtos: ProdutoConsDTO[] = [];
  adicionais: ProdutoDTO[] = [];
  comboCategorias: ComboDTO[] = [];

  //itens do pedido
  pedidoItem: PedidoItemDTO;
  adicionaisItem: ProdutoDTO[] = [];


  constructor(
    public produtoService: ProdutoService,
    public adicionalProduto: AdicionalProdutoService,
    public categoriaService: CategoriaService,
    public fb: FormBuilder) {
    super('pesquisarProdutoPopupModal');
  }

  ngOnInit() {
    this.criarForm();
  }
  
  show() {
    this.ctrExibicaoCampoOrdem = false;
    this.criarForm();
    this.showPopup("Pesquisa de Produto");
  }


  pesquisarProduto(form: any) {
    let idCategoria = '';
    if (form.categoria != '') {
      idCategoria = form.categoria[0].id;
    }
    this.qtAcompanhamento = 0;
    this.qtItens = 0;
    this.produtoService.findByCategoriaOrNmProduto(idCategoria, form.nmProduto, this.moduloConsulta).
      subscribe(
        res => this.produtos = res,
        error => TrataErrorService.tratarError(error)
      );
    this.adicionais = [];
  }

  selecionarProduto(produto: ProdutoConsDTO) {

    let prod = {} as ProdutoDTO;
    prod.id = produto.id;
    prod.nmProduto =  produto.nmProduto;
    prod.vlVenda = produto.vlVenda;
    
    this.desabilitarBtnSelecione = false;
    this.qtAcompanhamento = Number(produto.qtAcompanhamento)
    this.produto = prod;
    this.adicionais = [];
    if (this.ocultarAcompanhamento) {
      this.ctrExibicaoCampoOrdem = this.exibirCampoOrdem;
      if (this.cmpOrdemItem != undefined) {
        this.fieldFocus(this.cmpOrdemItem);
      }
    } else if (this.qtAcompanhamento > 0) {
      this.adicionalProduto.findByCategoria(produto.idCategoria)
        .subscribe(
          res => {
            for (let p of res) {
              let prod = {} as ProdutoDTO;
                prod.id = p.idProduto;
                prod.nmProduto = p.nmProduto;
              this.adicionais.push(prod);
            }
          },
          error => TrataErrorService.tratarError(error)
        )
      this.ctrExibicaoCampoOrdem = this.exibirCampoOrdem;
    }
  }


  btnConfirmar() {
    this.pedidoItem.qtItem = 1;
    this.pedidoItem.nrOrdemItem = this.formulario.get('nrOrdemItem').value;
    this.pedidoItem.destaque = this.formulario.get('destaque').value;
    this.pedidoItem.produto = this.produto;
    this.pedidoItem.adicionais = this.adicionaisItem;
    this.pedidoItem.hash = this.calcularHashItem(this.pedidoItem);
    console.log(this.pedidoItem);
    this.onClickConfirm.emit(this.pedidoItem);
    EventEmitterService.closePopup(this.nomeJanePopup);
  }

  private criarForm() {
    this.categoriaService.findCategoriasAtivasExcetoInterna(this.moduloConsulta)
      .subscribe(
        response => this.comboCategorias = this.preencharCombo(response, 'id', 'nmCategoria'),
        error => TrataErrorService.tratarError(error)
      );
    this.pedidoItem = {};
    this.produtos = [];
    this.adicionais = [];
    this.adicionaisItem = [];
    this.qtAcompanhamento = 0;
    this.qtItens = 0;
    this.formulario = this.fb.group({
      categoria: [''],
      nmProduto: [''],
      nrOrdemItem: [''],
      destaque: false
    })
  }

  consultarCategorias(modulo: any) {
    modulo.subscribe(
      response => this.comboCategorias = this.preencharCombo(response, 'id', 'nmCategoria'),
      error => TrataErrorService.tratarError(error)
    );

  }


  public selecionarItem(acao: boolean, item: ProdutoDTO) {

    if (acao) {
      if (this.qtItens < this.qtAcompanhamento) {
        item.selecionado = true;
        this.adicionaisItem.push(item);
        this.qtItens++;
      }
    } else {
      this.qtItens--;
      item.selecionado = false
      this.adicionaisItem = this.adicionaisItem.filter((adic: ProdutoDTO) => adic.id != item.id)
    }
  }


  verificarItem(item: ProdutoDTO): boolean {
    if (this.qtItens < this.qtAcompanhamento) {
      return false;
    } else {
      if (item.selecionado) {
        return false;
      }
    }
    return true;
  }

  getColor() {
    return this.formulario.get('destaque').value ? 'green' : 'gray';
  }

  definirCor() {
    this.formulario.patchValue({
      destaque: !this.formulario.get('destaque').value
    })
  }
 
  definirIcone(){
    return {
      'icon-star': !this.formulario.get('destaque').value, 
      //'fa fa-star fa-lg mt-0': this.formulario.get('destaque').value
      'fa fa-star': this.formulario.get('destaque').value
    }
  }


}



