import { TipoMensagemEnum } from './../enums/tipo-mensagem.enum';

export class ToasterMensagem{

    constructor(private tipo: TipoMensagemEnum, private titulo: string, private mensagem: string){ }

    public getTipoMensagem(){
        let array = [];
            array[TipoMensagemEnum.success] = 'success';
            array[TipoMensagemEnum.warning] = 'warning';
            array[TipoMensagemEnum.danger] = 'error';
            array[TipoMensagemEnum.info] = 'info';
        
            return array[this.tipo];

    }
    public getTitulo(){
        return this.titulo;
    }
    public getMensagem(){
        return this.mensagem;
    }

}




