import { ResponseContentType } from '@angular/http';
import { STORAGE_KEYS } from './storage-keys.config';
import { StorageService } from './../../services/storage.service';
import { HttpClient, HttpHeaders } from "@angular/common/http";

export abstract class AbstractService {



    constructor() { }

    getHearderToken(): {} {
        let token = this.getToken().token;
        let authHeader = { headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + token }) }
        return authHeader;
    }
    getPDF(): {} {
        // return { responseType: 'blob' };
        let authHeader = {
            responseType: 'blob',
            headers: new HttpHeaders({'Authorization': 'Bearer ' + this.getToken().token })
        }
        return authHeader;
    }

    getHearderTokenNoResponse(): {} {
        let authHeader = {
            observe: 'response',
            responseType: 'text',
            headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + this.getToken().token })
        }
        return authHeader;
    }



    getHeaderTokenOnly() {
        let token = this.getToken().token;
        return new HttpHeaders({ 'Authorization': 'Bearer ' + token })
    }
    public getToken() {
        let usr = localStorage.getItem(STORAGE_KEYS.localUser);
        if (usr == null) {
            return null;
        } else {
            return JSON.parse(usr);
        }

    }

}



