import { RequestOptions } from '@angular/http';
import { API_CONFIG } from './../config/core/api.config';
import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs/Rx";

@Injectable()
export class ImageService {

    private id: string;

    constructor(public http: HttpClient) {
    }

    public getImageFromBucket(id: string, modulo: string): Observable<any> {
        this.id = id;
        let url = this.recuperarUri(modulo);
        return this.http.get(url);
    }

    public getImage(id: string, modulo: string): string {
        this.id = id;
        return this.recuperarUri(modulo);
    }

    private recuperarUri(modulo: string): string {
        switch (modulo) {
            case 'CLI': return `${API_CONFIG.bucketBaseUrl}/clientes/cli${this.id}.jpg`;
            case 'PRD': return `${API_CONFIG.bucketBaseUrl}/produtos/prd${this.id}.jpg`;
            case 'CAT': return `${API_CONFIG.bucketBaseUrl}/categorias/cat${this.id}.jpg`;
        }
    }
}



