import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { LogService } from './../services/log.service';
import { NgModule, LOCALE_ID } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { AppComponent } from './app.component';

import { AuthModule } from './pages/auth/auth.module';
import { BsDropdownModule } from 'ng2-bootstrap/dropdown';
import { TabsModule } from 'ng2-bootstrap/tabs';
import { ChartsModule } from 'ng2-charts/ng2-charts';

import { SIDEBAR_TOGGLE_DIRECTIVES } from './shared/directive/sidebar.directive';
import { NAV_DROPDOWN_DIRECTIVES } from './shared/directive/nav-dropdown.directive';
import { AsideToggleDirective } from './shared/directive/aside.directive';

import { ToasterModule } from 'angular2-toaster/angular2-toaster';
import { AuthGuard } from './../config/guard/auth-guard.service';
import { StorageService } from './../services/storage.service';
import { AuthService } from './../services/auth.service';

// Routing Module
import { AppRoutingModule } from './app.routing';

//Layouts
import { LayoutModule } from './layout/layouts/layout.module';
import { CoreModule } from 'config/core/core-module';
import { ErrorInterceptorProvider } from 'config/interceptors/error-interceptor';
import { AuthInterceptorProvider } from 'config/interceptors/auth-interceptor';
import { p404Component } from './pages/admin/erros/404.component';
import { p500Component } from './pages/admin/erros/500.component';


@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    BsDropdownModule.forRoot(),
    TabsModule.forRoot(),
    ChartsModule,
    ToasterModule,
    HttpClientModule,
    LayoutModule,
    AuthModule
  ],
  declarations: [
    AppComponent,
    p404Component,
    p500Component
  ],
  providers: [{
    provide: LocationStrategy,
    useClass: HashLocationStrategy
  },
  AuthGuard,
  AuthService,
  StorageService,
  LogService,
  AuthInterceptorProvider,
  ErrorInterceptorProvider,
  {provide: LOCALE_ID, useValue: 'pt-BR'}
],
  bootstrap: [ AppComponent ]
})
export class AppModule { }



