import { AuthService } from 'services/auth.service';
import { UsuarioService } from './../usuario.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';
import { UsuarioDTO } from './../../../../../models/domain/usuario.dto';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { TrataErrorService } from 'services/trata-error.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { Route, Router } from '@angular/router';



@Component({
  templateUrl: './senha-usuario.component.html'
})
export class SenhaUsuarioComponent extends AbstractBaseComponent implements OnInit {


  @ViewChild('campoNome') campo;

  usuario: UsuarioDTO = {};

  constructor(
    public auth: AuthService,
    public fb: FormBuilder,
    public usuarioService: UsuarioService) {
    super();
  }

  ngOnInit() {
    this.criarForm();
    this.usuarioService.infoUsuarioLogado()
      .subscribe(res => { this.usuario = res; this.alterarForm(res) });
  }

  btnConfirmar(usuario: UsuarioDTO) {
    this.usuarioService.changePassword(usuario)
      .subscribe(
        res => {
          EventEmitterService.emitirMensagemToasterSucesso('Usuario', 'senha alterada com sucesso!!!')
          this.auth.logoutSistema()
        },
        error => TrataErrorService.tratarError(error));
  }


  private alterarForm(usuario: UsuarioDTO) {
    this.formulario.patchValue({
      id: usuario.id,
      dsLogin: usuario.dsLogin,
      dsSenha: usuario.dsSenha,
      senhaConfirmacao: usuario.senhaConfirmacao,
      emAlteracao: true
    })

  }

  private criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      dsLogin: [''],
      dsSenha: [''],
      senhaConfirmacao: [''],
      emAlteracao: false
    })
    if (this.campo != undefined) {
      this.fieldFocus(this.campo);
    }
  }
}


