import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';


import { API_CONFIG } from './../../../../config/core/api.config';
import { AbstractService } from 'config/core/abstract-service';
import { UsuarioDTO } from './../../../../models/domain/usuario.dto';


@Injectable()
export class UsuarioService extends AbstractService {

  url: string = `${API_CONFIG.baseUrl}/usuarios`;

  constructor(public http: HttpClient) {
    super();
  }

  findById(id: string): Observable<{}> {
    return this.http.get(`${this.url}/${id}`, this.getHearderToken());
  }

  findAll(): Observable<UsuarioDTO[]> {
    return this.http.get<UsuarioDTO[]>(this.url, this.getHearderToken());
  }

  infoUsuarioLogado(): Observable<UsuarioDTO> {
    return this.http.get<UsuarioDTO>(`${this.url}/usuariologado`, this.getHearderToken());
  }

  salvar(usuario: UsuarioDTO) {
    return this.http.post(`${this.url}`, JSON.stringify(usuario), this.getHearderTokenNoResponse());
  }

  changeStatus(usuario: UsuarioDTO) {
    return this.http.put(`${this.url}/${usuario.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
  }

  consultarPorCpf(nrCpf: string) {
    let cpf = nrCpf.replace(/\D/g, '');
    return this.http.get<UsuarioDTO>(`${this.url}/consultarCpf?cpf=${cpf}`, this.getHearderToken());
  }

  consultarPorLogin(dsLogin: string) {
    return this.http.get<UsuarioDTO>(`${this.url}/username?login=${dsLogin}`, this.getHearderToken());
  }

  consultarPorEdEmail(edEmail: string) {
    return this.http.get<UsuarioDTO>(`${this.url}/consultarEmail?edEmail=${edEmail}`, this.getHearderToken());
  }

  atualizar(usuario: UsuarioDTO) {
    return this.http.put(`${this.url}/${usuario.id}`, JSON.stringify(usuario), this.getHearderTokenNoResponse());
  }

  changePassword(usuario: UsuarioDTO) {
    return this.http.put(`${this.url}/${usuario.id}/changepassword`, JSON.stringify(usuario), this.getHearderTokenNoResponse());
  }

  excluir(usuario: UsuarioDTO): Observable<{}> {
    return this.http.delete(`${this.url}/${usuario.id}`, this.getHearderToken());
  }

}



