import { UsuarioService } from './../usuario.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { API_CONFIG } from 'config/core/api.config';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { UsuarioDTO } from './../../../../../models/domain/usuario.dto';
import { TrataErrorService } from './../../../../../services/trata-error.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';

@Component({
  templateUrl: './listar-usuario.component.html',
  styleUrls: ['./listar-usuario.component.scss']
})
export class ListarUsuarioComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeStatusModal') popupChangeStatus;

  cliente: UsuarioDTO;
  items: UsuarioDTO[] = [];

  constructor(public usuarioService: UsuarioService, public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos()
  }

  selecionarItem(item: UsuarioDTO) {
    this.cliente = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: UsuarioDTO) {
    this.cliente = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.usuarioService.changeStatus(this.cliente)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Usuário', 'Status do usuário foi alterado com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  excluir() {
    this.usuarioService.excluir(this.cliente)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Usuário', 'Usuário foi excluído com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  private carregarTodos() {
    this.usuarioService.findAll().subscribe(
      response => {this.items = response},
      error => TrataErrorService.tratarError(error));
  }

}



