import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { API_CONFIG } from './../../../../config/core/api.config';
import { AbstractService } from 'config/core/abstract-service';

import { ProdutoDTO } from 'models/domain/produto.dto';
//import { catchError, retry } from 'rxjs/operators';


@Injectable()
export class ProdutoService extends AbstractService {

  url: string = `${API_CONFIG.baseUrl}/produtos`;

  constructor(public http: HttpClient) {
    super();
  }

  findById(id: string): Observable<{}> {
    return this.http.get(`${this.url}/${id}`, this.getHearderToken());
  }

  findAll(): Observable<ProdutoDTO[]> {
    return this.http.get<ProdutoDTO[]>(`${this.url}/`,this.getHearderToken());
  }

  findByCategorias(categorias: string[]): Observable<ProdutoDTO[]> {
    let ids: string = '';
    for (let s of categorias) { ids += s+','; }
    return this.http.get<ProdutoDTO[]>(`${this.url}/consultarPorCategorias?categorias=${ids}`, this.getHearderToken());
  }
  
  findByCategoriaOrNmProduto(idCategoria: string, nmProduto: string, moduloConsulta: string): Observable<ProdutoDTO[]> {
    if (moduloConsulta =='CRD'){
      return this.http.get<ProdutoDTO[]>(`${API_CONFIG.baseUrl}/cardapio/consultarPor?idCategoria=${idCategoria}&nmProduto=${nmProduto}`);
    }else{
      return this.http.get<ProdutoDTO[]>(`${this.url}/consultarPor?idCategoria=${idCategoria}&nmProduto=${nmProduto}`, this.getHearderToken());
    }
  }

  findAllProdutoInterno(): Observable<ProdutoDTO[]> {
    return this.http.get<ProdutoDTO[]>(`${this.url}/internos`, this.getHearderToken());
  }

  salvar(produto: ProdutoDTO) {
    return this.http.post(`${this.url}/`, JSON.stringify(produto), this.getHearderTokenNoResponse());
  }

  changeStatus(produto: ProdutoDTO) {
    return this.http.put(`${this.url}/${produto.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
  }

  atualizar(produto: ProdutoDTO) {
    return this.http.put(`${this.url}/${produto.id}`, JSON.stringify(produto), this.getHearderTokenNoResponse());
  }

  excluir(produto: ProdutoDTO): Observable<{}> {
    let url = `${this.url}/${produto.id}`;
    return this.http.delete(url, this.getHearderToken());
  }

}



