import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'form-buttons',
    template: `
      <div class="hidden-md-down">
          <button type="button" *ngIf="!ocultarBtnEditar" class="btn btn-sm btn-primary cursor-hands" (click)="btnEditar()">
              <i class="fa fa fa-edit"> </i>
          </button>
          <button type="button" *ngIf="exibirBtnAlterarSenha" class="btn btn-sm btn-primary cursor-hands" (click)="btnAlterarSenha()">
              <i class="fa fa fa-edit"> </i>
          </button>
          <button type="button" *ngIf="!ocultarBtnExcluir" class="btn btn-sm btn-danger cursor-hands" (click)="btnExcluir()">
              <i class="fa fa-eraser"> </i>
          </button>
          <button type="button" *ngIf="!ocultarBtnAlterarStatus" class="btn btn-sm btn-warning cursor-hands" (click)="btnAlterarStatus()">
              <i class="fa fa-exclamation"> </i>
          </button>
      </div>
      <div dropdown placement="bottom right" class="hidden-lg-up">
        <button type="button" class="btn btn-transparent" style="color:black" dropdownToggle>
            <i class="icon-options-vertical"></i>
        </button>
        <div class="dropdown-menu dropdown-menu-right" *dropdownMenu>
            <a *ngIf="!ocultarBtnEditar" class="dropdown-item cursor-hands"         (click)="btnEditar()">editar</a>
            <a *ngIf="exibirBtnAlterarSenha" class="dropdown-item cursor-hands"     (click)="btnAlterarSenha()">alterar senha</a>
            <a *ngIf="!ocultarBtnExcluir" class="dropdown-item cursor-hands"        (click)="btnExcluir()" >excluir </a>
            <a *ngIf="!ocultarBtnAlterarStatus" class="dropdown-item cursor-hands"  (click)="btnAlterarStatus()" >alterar status</a>
        </div>
      </div>
`
})
export class FormButtonsComponent implements OnInit {

    @Input() ocultarBtnEditar: boolean = false;
    @Input() ocultarBtnExcluir: boolean = false;
    @Input() ocultarBtnAlterarStatus: boolean = false;
    @Input() exibirBtnAlterarSenha: boolean = false;
    
    @Output() onClickBtnEditar = new EventEmitter<boolean>();
    @Output() onClickBtnExcluir = new EventEmitter<boolean>();
    @Output() onClickBtnAlterarStatus = new EventEmitter<boolean>();
    @Output() onClickBtnAlterarSenha = new EventEmitter<boolean>();

    constructor() { }

    ngOnInit() {
    }




    btnEditar() {
        this.onClickBtnEditar.emit(true);
    }

    btnExcluir() {
        this.onClickBtnExcluir.emit(true);
    }

    btnAlterarStatus() {
        this.onClickBtnAlterarStatus.emit(true);
    }
    btnAlterarSenha() {
        this.onClickBtnAlterarSenha.emit(true);
    }
}



