import { API_CONFIG } from 'config/core/api.config';
import { STORAGE_KEYS } from './../config/core/storage-keys.config';
import { FieldMessage } from './../models/config/field-message';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { StorageService } from './storage.service';
import { Injectable } from '@angular/core';

export class TrataErrorService {

  constructor() { }

  static tratarError(error: any): any {
    let errorObj = error;
    if (errorObj.error) {
      errorObj = errorObj.error;
    }
    if (!errorObj.status) {
      errorObj = JSON.parse(errorObj);
    } 
    if (errorObj.status == undefined) {
      errorObj.status = 0;
    }
    switch (errorObj.status) {
      case 0:
        EventEmitterService.emitirMensagemToasterDanger("Alerta", `Erro: ${errorObj.status} - Mensagem: rede indisponível`);
        break;
      case 400:
        EventEmitterService.emitirMensagemToasterDanger("Alerta", `Erro: ${errorObj.status} - Mensagem: ${errorObj.message}`);
        break;
      case 401:
        this.emitirMensagemError("Erro: 401 - Email ou senha incorreto");
        break;
      case 403:
        let requestToAPI = errorObj.url.substring(0, API_CONFIG.bucketBaseUrl.length) == API_CONFIG.bucketBaseUrl;
        if (!requestToAPI){
            localStorage.removeItem(STORAGE_KEYS.localUser);
        }
        break;
      case 422:
        EventEmitterService.getErrors().emit(errorObj.errors);
        break;
      default:
        EventEmitterService.emitirMensagemToasterAlerta("Alerta", `Erro: ${errorObj.status} - Messagem: ${errorObj.message}`);
    }

  }

  private static handleMessage(msg: string) {
    let errors: FieldMessage[] = [];
    let error = {} as FieldMessage;
    error.message = msg;
    errors.push(error);
    EventEmitterService.getErrors().emit(errors);
  }

  public static emitirMensagemError(msg: string) {
    this.handleMessage(msg);
  }

  public static limparMensagemError() {
    EventEmitterService.getErrors().emit({});
  }






}


